.heading{
    font-size: 3.3rem;
    font-family: 'Roboto Condensed', sans-serif;
    /* display: inline-block; */
    padding-bottom: 5px;
    position: relative;
    text-align: center;
}

.heading:before{
    content: "";
    position: absolute;
    width: 50%;
    height: 1px;
    bottom: 0;
    left: 0;
    border-bottom: 4px solid white;
}
.heading2:before{
    content: "";
    position: absolute;
    width: 35%;
    height: 1px;
    bottom: 0;
    left: 200px;
    border-bottom: 4px solid #f3ba2f;
}
